.App {
  font-size: larger;
  margin-left: 20px;
}

.hidden {
  display: none;
}

.button--start {
  width: 100px;
}

.button {
  height: 40px;
  margin: 0 5px;
  padding: 10px;

  color: #f0f8ff;
  border-radius: 10px;
  border: none;
  background-color: #3751ff;
  cursor: pointer;
}

.button--reset {
  border: 1px solid #3751ff;
  color: #3751ff;
  background-color: #fff;
}
